import { Token } from '@lumino/coreutils';
import { ILayoutRestorer } from '@jupyterlab/application';
import { MainAreaWidget, ICommandPalette, WidgetTracker } from '@jupyterlab/apputils';
import { ISettingRegistry } from '@jupyterlab/settingregistry';
import { ILauncher } from '@jupyterlab/launcher';
import icon from '@datalayer/icons-react/data2/TreeDeciduousIconJupyterLab';
import { requestAPI } from './handler';
import { JupyterEnvironmentsWidget } from './widget';
import { Timer } from "../state/zustand";
import { TimerView } from "../timer/TimerView";
import { mobxTimer, MobxTimerView } from "../state/mobx";
import '../../style/index.css';
export const IJupyterEnvironments = new Token('@datalayer/jupyter-environments:plugin');
/**
 * The command IDs used by the plugin.
 */
var CommandIDs;
(function (CommandIDs) {
    CommandIDs.create = 'create-jupyter-environments-widget';
})(CommandIDs || (CommandIDs = {}));
/**
 * Initialization data for the @datalayer/jupyter-environments extension.
 */
const plugin = {
    id: '@datalayer/jupyter-environments:plugin',
    autoStart: true,
    requires: [ICommandPalette],
    optional: [ISettingRegistry, ILauncher, ILayoutRestorer],
    provides: IJupyterEnvironments,
    activate: (app, palette, settingRegistry, launcher, restorer) => {
        const { commands } = app;
        const command = CommandIDs.create;
        const tracker = new WidgetTracker({
            namespace: 'jupyter-environments',
        });
        if (restorer) {
            void restorer.restore(tracker, {
                command,
                name: () => 'jupyter-environments',
            });
        }
        const jupyterEnvironments = {
            timer: new Timer(),
            TimerView,
            mobxTimer,
            MobxTimerView,
        };
        commands.addCommand(command, {
            caption: 'Show Jupyter Environments',
            label: 'Jupyter Environments',
            icon,
            execute: () => {
                const content = new JupyterEnvironmentsWidget(app, jupyterEnvironments);
                const widget = new MainAreaWidget({ content });
                widget.title.label = 'Jupyter Environments';
                widget.title.icon = icon;
                app.shell.add(widget, 'main');
                tracker.add(widget);
            }
        });
        const category = 'Datalayer';
        palette.addItem({ command, category });
        const settingsUpdated = (settings) => {
            const showInLauncher = settings.get('showInLauncher').composite;
            if (launcher && showInLauncher) {
                launcher.add({
                    command,
                    category,
                    rank: 5.2,
                });
            }
        };
        if (settingRegistry) {
            settingRegistry
                .load(plugin.id)
                .then(settings => {
                console.log('@datalayer/jupyter-environments settings loaded:', settings.composite);
                settingsUpdated(settings);
                settings.changed.connect(settingsUpdated);
            })
                .catch(reason => {
                console.error('Failed to load settings for @datalayer/jupyter-environments.', reason);
            });
        }
        requestAPI('config')
            .then(data => {
            console.log(data);
        })
            .catch(reason => {
            console.error(`Error while accessing the jupyter server jupyter_environments extension.\n${reason}`);
        });
        console.log('JupyterLab plugin @datalayer/jupyter-environments:plugin is activated.');
        return jupyterEnvironments;
    }
};
export default plugin;
